import React from 'react'
import Time from './Switch_button'
import { NavLink, Link } from 'react-router-dom'
import './index.css'


function Heading() {
    return (
        <header>
            <div className="item-1">

                <Link to="/"><h1>My Movies List</h1></Link>

                <div className="links">
                    <NavLink exact to="/" activeClassName="active_class" id='link-1'>
                        <h3>Home</h3>
                    </NavLink>

                    <NavLink exact to="/about" activeClassName="active_class" id='link-2'>
                        <h3>About</h3>
                    </NavLink>
                </div>
            </div>

            <div className="item-2">
                <Time />
                <div id="tr-icon">
                    <div className="dropdown">
                        <h1>&#9776;</h1>
                        <div className="dropdown-content">
                            <NavLink exact to="/" id='link-1'>
                                <h3>Home</h3>
                            </NavLink>

                            <NavLink to="/about" id='link-2'>
                                <h3>About</h3>
                            </NavLink>
                        </div>
                    </div>
                </div>
            </div>
        </header>
    );
}

export default Heading;