import React from 'react'
import Card from './Card'
import content from './Content'


let card = (content) => {
    return (
        <Card
            key={content['key']}
            img={content['img']}
            title={content['title']}
            type={content['type']}
            src={content['src']}
            ott={content['ott']} />
    )

}


function MainContent() {
    return (
        <>
            <section id="main-content">
                <div className="all-items">
                    {content.map(card)}

                </div>
            </section>
        </>
    )
}

export default MainContent;